import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpRequest} from '@angular/common/http';
import {map} from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class BasicAuthService {
  private _url = 'https://proxes.aex.co.za';
  private  Authorization= 'Authorization';

  constructor(private _http: HttpClient) {
    console.log('Test');
    if (!localStorage.getItem(this.Authorization)) {


      let username = prompt('Username');
      let password = prompt('Password');


      this.login(username, password);
    }
  }

  get(): any {
    const headers_object = new HttpHeaders();
    headers_object.append('Content-Type', 'application/json');
    headers_object.append(this.Authorization, localStorage.getItem(this.Authorization));

    const httpOptions = {
      headers: headers_object,
      withCredentials: true
    };

    return this._http.get(this._url + '/_search?size=100', httpOptions);
  }

  search(queryText): any {
    const headers_object = new HttpHeaders();
    headers_object.append('Content-Type', 'application/json');
    headers_object.append(this.Authorization, localStorage.getItem(this.Authorization));

    const httpOptions = {
      headers: headers_object,
      withCredentials: true
    };
    return this._http.get(this._url + '/_search?size=100&q=_all:' + queryText, httpOptions);
  }

  login(username: string, password: string) {

    localStorage.setItem(this.Authorization, 'Basic ' + btoa(username + ':' + password));

  }

  logout() {
    // remove user from local storage to log user out
    localStorage.removeItem(this.Authorization);
  }
}
